
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@500&display=swap');

* {
  width: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
}


body {
  width: 100%;
  display: flex;
  overflow: hidden;
}

.registration-from {
  width: 100%;
  height: 100vh;
  background: linear-gradient(to top left, #fdfc47, #24fe41);
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: visible;

}

.container {
  width: 50%;
  height: 85vh;
  background-color: rgb(255, 255, 255);
  border: none;
  border-radius: 10px;
  padding: 20px 20px 10px 20px;
  overflow-y: scroll;

}

.container::-webkit-scrollbar {
  width: 0px;
}

.container:hover {
  box-shadow: 0px 0px 35px 2px rgba(0, 0, 0, 0.16);
}

h1 {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  font-size: 20px;
  padding-bottom: 20px;
}

.form-fields {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.input-areas {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
}

.input-areas1 {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
}

.input-areas2 {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
}

.input-areas3 {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.input-areas4 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
}

.input-areas1 div:nth-of-type(1) div {
  width: 100%;
  height: 40px;
  display: flex;
  flex-direction: row;
  list-style: none;
  align-items: flex-start;
}

.input-areas1 li {
  width: 40%;
  display: flex;
  align-items: center;
  margin-right: 20px;
}

li input {
  width: 20px;
}

form .input-areas div {
  display: flex;
  flex-direction: column;
  width: 49%;
}

form .input-areas1 div {
  display: flex;
  flex-direction: column;
  width: 49%;
}

form .input-areas2 div {
  display: flex;
  flex-direction: column;
  width: 49%;
}

form .input-areas3 div {
  display: flex;
  flex-direction: column;
  width: 100%;
}

form .input-areas4 div {
  display: flex;
  flex-direction: column;
  width: 32%;
}

input {
  height: 40px;
  border: thin solid #c9ffd0;
  border-radius: 5px;
  padding: 10px;
  margin: 8px 10px 10px 0px;
  font-size: 15px;
}

input:focus {
  outline: none;
}

.form-action-buttons {
  width: 100%;
  display: flex;
  align-items: flex-end;
}

.form-action-buttons input {
  background-color: black;
  color: white;
  width: 50%;
  font-size: 13px;
  cursor: pointer;
  outline: none;
  transition: all .15s ease-out;
}

.form-action-buttons input:active {
  transform: scale(0.97);

}

.form-action-buttons div {
  width: 50%;
  margin: 10px 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 5px;
  border: thin solid #c9ffd0;
  background-color: black;
  color: white;
  font-size: 13px;
  text-align: center;
  cursor: pointer;
  transition: all .15s ease-out;
}

.form-action-buttons div:active {
  transform: scale(0.97);
}

.form-action-buttons a {
  text-decoration: none;
  width: 50%;
  height: 40px;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 5px;
  border: thin solid #c9ffd0;
  background-color: black;
  color: white;
  font-size: 13px;
  text-align: center;
  cursor: pointer;
  transition: all .15s ease-out;
}

.form-action-buttons a:active {
  transform: scale(0.97);
}




.validationerr {
  border: 2px solid #eb3030;
}

.noerr {
  border: thin solid #c9ffd0;
}

.noerr:focus {
  border: thin solid green;
}

#firstnameerr, #lastnameerr, #gendererr,
#doberr, #addresserr, #mobileerr, #emailerr,
#districterr, #stateerr, #pincodeerr {
  color: red;
  font-size: 10px;
  margin-left: 2px;
  transition: all .15s ease-out;
}

.spanerr {
  opacity: 0%;
}

.spannoerr {
  opacity: 100%;
}

label span:nth-of-type(1) {
  color: red;
}